import React from "react"
import DarkModeToggle from "react-dark-mode-toggle"
import Navbar from "./nav"
import Footer from "./footer"
import Head from "./head"
import {useDarkMode} from "./customHook"

// styling
import { GlobalStyles } from "./globalStyle"
import { darkTheme, lightTheme } from "./theme"
import { ThemeProvider } from "styled-components"

export default function Layout(props) {
  const [isDarkMode, setIsDarkMode] = useDarkMode()

  return (
    <ThemeProvider theme={JSON.parse(isDarkMode) === false ? lightTheme : darkTheme}>
      <div>
        <GlobalStyles />
        <Head />
        <Navbar />
        <div className="darkmode-container">
        <DarkModeToggle
          className='darkmode-container'
          onChange={setIsDarkMode}
          checked={JSON.parse(isDarkMode)}
          size={80}
          style={{padding:'30px'}}
        />
        </div>
        <div className="layout-container app-container">{props.children}</div>

        <Footer />
      </div>
    </ThemeProvider>
  )
}
